<h1 id="模板消息" tabindex="-1"><a class="header-anchor" href="#模板消息" aria-hidden="true">#</a> 模板消息</h1>
<p>模板消息仅用于公众号向用户发送重要的服务通知，只能用于符合其要求的服务场景中，如信用卡刷卡通知，商品购买成功通知等。不支持广告等营销类消息以及其它所有可能对用户造成骚扰的消息。</p>
<h2 id="修改账号所属行业" tabindex="-1"><a class="header-anchor" href="#修改账号所属行业" aria-hidden="true">#</a> 修改账号所属行业</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">setIndustry</span><span class="token punctuation">(</span><span class="token variable">$industryId1</span><span class="token punctuation">,</span> <span class="token variable">$industryId2</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="获取支持的行业列表" tabindex="-1"><a class="header-anchor" href="#获取支持的行业列表" aria-hidden="true">#</a> 获取支持的行业列表</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">getIndustry</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="添加模板" tabindex="-1"><a class="header-anchor" href="#添加模板" aria-hidden="true">#</a> 添加模板</h2>
<p>在公众号后台获取 <code>$shortId</code> 并添加到账户。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">addTemplate</span><span class="token punctuation">(</span><span class="token variable">$shortId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="获取所有模板列表" tabindex="-1"><a class="header-anchor" href="#获取所有模板列表" aria-hidden="true">#</a> 获取所有模板列表</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">getPrivateTemplates</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="删除模板" tabindex="-1"><a class="header-anchor" href="#删除模板" aria-hidden="true">#</a> 删除模板</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">deletePrivateTemplate</span><span class="token punctuation">(</span><span class="token variable">$templateId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="发送模板消息" tabindex="-1"><a class="header-anchor" href="#发送模板消息" aria-hidden="true">#</a> 发送模板消息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'touser'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'user-openid'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'template_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'template-id'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'https://easywechat.org'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'miniprogram'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
                <span class="token string single-quoted-string">'appid'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxx'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'pagepath'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pages/xxx'</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'key1'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'VALUE'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'key2'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'VALUE2'</span><span class="token punctuation">,</span>
            <span class="token operator">...</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><blockquote>
<p>如果 url 和 miniprogram 字段都传，会优先跳转小程序。</p>
</blockquote>
<h2 id="发送一次性订阅消息" tabindex="-1"><a class="header-anchor" href="#发送一次性订阅消息" aria-hidden="true">#</a> 发送一次性订阅消息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">template_message</span><span class="token operator">-></span><span class="token function">sendSubscription</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'touser'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'user-openid'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'template_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'template-id'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'https://easywechat.org'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'scene'</span> <span class="token operator">=></span> <span class="token number">1000</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'key1'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'VALUE'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'key2'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'VALUE2'</span><span class="token punctuation">,</span>
            <span class="token operator">...</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><blockquote>
<p>如果你想为发送的内容字段指定颜色，你可以将 &quot;data&quot; 部分写成下面 4 种不同的样式，不写 <code>color</code> 将会是默认黑色：</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'foo'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'你好'</span><span class="token punctuation">,</span>  <span class="token comment">// 不需要指定颜色</span>
    <span class="token string single-quoted-string">'bar'</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'你好'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'#F00'</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token comment">// 指定为红色</span>
    <span class="token string single-quoted-string">'baz'</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'value'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'你好'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'color'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'#550038'</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token comment">// 与第二种一样</span>
    <span class="token string single-quoted-string">'zoo'</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'value'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'你好'</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token comment">// 与第一种一样</span>
<span class="token punctuation">]</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div>